import { useExperiment } from '@toggl/ab-testing';
import { isFirefox } from '@src/lib/utils';
import { Db } from '@src/lib/db';
import React from 'react';

export const useReminderNotificationExperiment = () => {
  const result = useExperiment({
    key: 'reminder-notification',
    variations: ['control', 'variant'],
    active: !isFirefox(),
  });

  React.useEffect(() => {
    if (result.value === 'variant') {
      void Db.get('reminderManuallyEdited').then(edited => {
        if (!edited) Db.set('remindersCheckEnabled', true);
      });
    }
  }, [result.value]);

  return result;
};
